/* 
动态菜单-全部的菜单导航 除了首页登录
*/
//商品管理
const Product = () => import("@/views/product/Index.vue");
const List = () => import("@/views/product/list/Index.vue");
const Category = () => import("@/views/product/category/Index.vue");
const AddProduct =()=>import('@/views/product/list/AddProduct.vue')
//订单
const Order = () =>import('@/views/order/Index.vue')
const OrderList = () =>import('@/views/order/list/Index.vue')
const Collect = () =>import('@/views/order/collect/Index.vue')
const Contract=()=>import('@/views/order/contract/Index.vue')
//广告
const Advert = () =>import('@/views/advert/Index.vue')
const AdvertList = () =>import('@/views/advert/list/Index.vue')

//管理员统计
import SystemManage from '@/views/SystemManage'
import department from '@/views/SystemManage/department'
import role from '@/views/SystemManage/role'
import whitelist from '@/views/SystemManage/whitelist'

//用户统计
import player from '@/views/player/Index.vue'
import playerInfo from '@/views/player/info'
import playerManage from '@/views/player/manage'
import playerOnline from '@/views/player/online'
import playerVIP from '@/views/player/vip'
import playerLottery from '@/views/player/lottery'
import playerCoin from '@/views/player/coin'

//数据统计
import statistics from '@/views/statistics/Index.vue'
import recharge from '@/views/statistics/recharge'
import stuff from '@/views/statistics/stuff'

//系统管理
import config from '@/views/config/Index.vue'
import table from '@/views/config/table'
import value from '@/views/config/value'
//公告管理
import announcement from '@/views/announcement/Index.vue'
import gameAnnouncement from '@/views/announcement/game'
import systemAnnouncement from '@/views/announcement/system'
import mail from '@/views/announcement/mail'

//充值
import rechargeAll from '@/views/recharge/Index.vue'
import record from '@/views/recharge/record'
import rechargeStatistics from '@/views/recharge/statistics'
import report from '@/views/recharge/report'

//渠道管理
import channel from '@/views/channel/Index.vue'
import channelConfig from '@/views/channel/config'
import task from '@/views/channel/task'
import compared from '@/views/channel/compared'
import userManage from '@/views/channel/user'
import configManage from '@/views/channel/configmanage'


export const menu = [
  {
    path: "/product", //产品管理
    name: "product",
    component: Product,
    redirect: "/product/list",
    meta: {
      title: "产品管理",
    },
    children: [
      {
        path: "list", //访问路径： /product/list
        name: "list",
        component: List,
        meta: {
          title: "产品列表",
        },
      },
      {
        path: "category",
        name: "category",
        component: Category,
        meta: {
          title: "产品分类",
        },
      },
      {
        path: "add-product", //商品添加--编辑-查看  /product/product-page
        name: "ProductPage",
        component: AddProduct,
        meta: {
          //配置高亮标识
          // activeMenu: "/product/list",//获取path
          activeMenu: "list",//获取name
          title: "添加商品",
        },
      },
    ],
  },
  {
    path: "/order", //订单管理
    name: "order",
    component: Order,
    redirect: "/order/order-list",
    meta: {
      title: "订单管理",
    },
    children: [
      {
        path: "order-list",
        name: "order-list",
        component: OrderList,
        meta: {
          title: "订单列表",
        },
      },
      {
        path: "collect",
        name: "collect",
        component: Collect,
        meta: {
          title: "汇总清单",
        },
      },
      {
        path: "contract",
        name: "contract",
        component: Contract,
        meta: {
          title: "订单审核",
        },
      },
    ],
  },
  {
    path: "/advert", //广告管理
    name: "advert",
    component: Advert,
    meta: {
      title: "广告分类",
    },
    children: [
      {
        path: "advert-list",
        name: "advert-list",
        component: AdvertList,
        meta: {
          title: "广告列表",
        },
      },
    ],
  },
  {
    path: "/player", //用户统计
    name: "player",
    component: player,
    meta: {
      title: "用户统计",
    },
    children: [
      {
        path: "player-info",
        name: "player-info",
        component: playerInfo,
        meta: {
          title: "用户信息",
        },
      },
      {
        path: "player-manage",
        name: "player-manage",
        component: playerManage,
        meta: {
          title: "用户管理",
        },
      },
      {
        path: "player-online",
        name: "player-online",
        component: playerOnline,
        meta: {
          title: "实时在线",
        },
      },
      {
        path: "player-vip",
        name: "player-vip",
        component: playerVIP,
        meta: {
          title: "VIP等级分布",
        },
      },
      {
        path: "player-lottery",
        name: "player-lottery",
        component: playerLottery,
        meta: {
          title: "奖券记录",
        },
      },
      {
        path: "player-coin",
        name: "player-coin",
        component: playerCoin,
        meta: {
          title: "金币记录",
        },
      },
    ],
  },
  {
    path: "/system",
    name: "system",
    component: SystemManage,
    redirect: "/system/role",
    meta: {
      title: "管理员",
    },
    children: [
      {
        path: "role",
        name: "role",
        component: role,
        meta: {
          title: "管理员信息",
        },
      },
      {
        path: "department",
        name: "department",
        component: department,
        meta: {
          title: "权限管理",
        },
      },
      {
        path: "whitelist",
        name: "whitelist",
        component: whitelist,
        meta: {
          title: "白名单",
        },
      }
    ],
  },
  {
    path: "/statistics",
    name: "statistics",
    component: statistics,
    redirect: "/statistics/recharge",
    meta: {
      title: "数据统计",
    },
    children: [
      {
        path: "recharge",
        name: "recharge",
        component: recharge,
        meta: {
          title: "每日充值统计",
        },
      },
      {
        path: "stuff",
        name: "stuff",
        component: stuff,
        meta: {
          title: "物品记录",
        },
      },
    ],
  },
  {
    path: "/config",
    name: "config",
    component: config,
    redirect: "/config/table",
    meta: {
      title: "系统管理",
    },
    children: [
      {
        path: "table",
        name: "table",
        component: table,
        meta: {
          title: "更新数据表",
        },
      },
      {
        path: "value",
        name: "value",
        component: value,
        meta: {
          title: "数值表配置",
        },
      },
    ],
  },
  {
    path: "/announcement",
    name: "announcement",
    component: announcement,
    redirect: "/announcement/gameAnnouncement",
    meta: {
      title: "运营管理",
    },
    children: [
      {
        path: "gameAnnouncement",
        name: "gameAnnouncement",
        component: gameAnnouncement,
        meta: {
          title: "游戏公告",
        },
      },
      {
        path: "systemAnnouncement",
        name: "systemAnnouncement",
        component: systemAnnouncement,
        meta: {
          title: "系统公告",
        },
      },
      {
        path: "mail",
        name: "mail",
        component: mail,
        meta: {
          title: "邮件系统",
        },
      },
    ],
  },
  {
    path: "/rechargeAll",
    name: "rechargeAll",
    component: rechargeAll,
    redirect: "/rechargeAll/record",
    meta: {
      title: "充值",
    },
    children: [
      {
        path: "record",
        name: "record",
        component: record,
        meta: {
          title: "充值记录",
        },
      },
      {
        path: "rechargeStatistics",
        name: "rechargeStatistics",
        component: rechargeStatistics,
        meta: {
          title: "充值统计",
        },
      },
      {
        path: "report",
        name: "report",
        component: report,
        meta: {
          title: "月收益报表",
        },
      },
    ],
  },
  {
    path: "/channel",
    name: "channel",
    component: channel,
    redirect: "/channel/channelConfig",
    meta: {
      title: "渠道管理",
    },
    children: [
      {
        path: "channelConfig",
        name: "channelConfig",
        component: channelConfig,
        meta: {
          title: "渠道配置",
        },
      },
      {
        path: "task",
        name: "task",
        component: task,
        meta: {
          title: "渠道任务",
        },
      },
      {
        path: "compared",
        name: "compared",
        component: compared,
        meta: {
          title: "渠道对比",
        },
      },
      {
        path: "userManage",
        name: "userManage",
        component: userManage,
        meta: {
          title: "渠道用户管理",
        },
      },
      {
        path: "configManage",
        name: "configManage",
        component: configManage,
        meta: {
          title: "渠道配置管理",
        },
      },
    ],
  },
];
